
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { ArticleUrl } from '@/api/api'
import { getColumnDetail } from '../api/api.js'
let p = 1
export default {
data() {
return {
StartPageY: 0,
HasData: true,
LoadMoreIcon: true,
LoadMoreText: '暂无数据',
ArticleList: [],
options: {
page: 1,
pagesize: 20
},
HeaderImg: '',
listData: []
}
},
components: {
XDialog,
LoadMore
},
mounted() {
if (this.$route.query.id) {
this.ArticleAjax()
}
},
methods: {
calcUpdate(date) {
var time = new Date().getTime()
time = parseInt((time - date * 1000) / 1000)
var s
if (time < 60 * 10) {
return '刚刚'
} else if (time < 60 * 60 && time >= 60 * 10) {
s = Math.floor(time / 60)
return s + '分钟前'
} else if (time < 60 * 60 * 24 && time >= 60 * 60) {
s = Math.floor(time / 60 / 60)
return s + '小时前'
} else if (time < 60 * 60 * 24 * 3 && time >= 60 * 60 * 24) {
s = Math.floor(time / 60 / 60 / 24)
return s + '天前'
} else {
var date = new Date(parseInt(date) * 1000)
return (
date.getFullYear() +
'/' +
(date.getMonth() + 1) +
'/' +
date.getDate()
)
}
},
ArticleAjax() {
let id = this.$route.query.id
this.$http({
method: 'GET',
url: getColumnDetail,
params: Object.assign({ id }, this.options)
}).then(res => {
const jsonpReturn = data => {
return data
}
let data = eval(res.body)
this.HeaderImg = this.urlProcess(data.data.channel.image)
delete data.data.channel
Object.keys(data.data).forEach(key => {
this.listData.push(data.data[key])
})
if (Object.keys(data.data) <= 0) {
this.HasData = false
}
})
},
urlProcess(url) {
if (url.match(/^http/) && url.match(/^http/).length > 0) {
return url
}
return 'http://jiaxinggongan.jxletong.com' + url
},
MoveStart(e) {
this.StartPageY = e.touches[0].pageY
},
MoveEnd(e) {
let distance = Math.abs(this.StartPageY - e.changedTouches[0].pageY)
if (distance > 100) {
this.LoadMore()
}
},
LoadMore() {
if (this.HasData) {
this.options.page++

this.ArticleAjax()

}
}
}
}



